.header-color {
	background: #bdc3c7; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #2c3e50, #bdc3c7);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid {
	background: #bdc3c7; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#2c3e50,
		#bdc3c7
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	position: fixed;
	text-align: center;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.avatar-img {
	height: 180px;
	padding-top: 4em;
}
.banner-text {
	background-color: black;
	opacity: .8;
	width: 75%;
	margin: auto;
	border-radius: 10px;
}
.banner-text h1 {
	font-size: 66px;
	font-weight: bold;
	color: white;
}
.banner-text hr {
	border-top: 5px dotted white;
	width: 50%;
	margin: auto;
}
.banner-text p {
	color: white;
	font-size: 20px;
	padding: 1em;
}
.social-links {
	display: flex;
	justify-content: space-between;
	width: 60%;
	margin: auto;
}
.social-links i {
	color: blanchedalmond;
	font-size: 5em;
	padding: 8px;
}
.projects-grid {
	display: flex;
}
.border {
border-radius: 25% 10%;
}
.particles {
	position: absolute;
	right: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	width: '100%';
	background-size: cover;
  }
.contact-body {
	margin: auto;
	background: lightslategrey;
	position: flex;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 4em;
}
.contact-grid {
	text-align: center;
	justify-content: center;
	width: 80%;
	height: auto;
	background: white;
	color: black;
	border: 2px solid grey;
	margin-bottom: 20px;
	border-radius: 2em;
}
.contact-grid h2 {
	font-family: 'Anton', sans-serif;
}
.contact-grid p {
	font-family: 'Oxygen'sans-serif;
	font-weight: bold;
}
.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto;
}
.contact-list i {
	font-size: 50px;
	padding-right: 5rem;
}
.resume-right-col {
	background: grey;
	color: white;
	padding: 2em;
}

@media (min-width: 600px) and (max-width: 700px) {
	.banner-text h1 {
		font-size: 3em;
	}
	.social-links i {
		font-size: 3em;
		padding: 4px;
	}
	.banner-text p {
		font-size: 20px;
	}
}
@media (min-width: 400px) and (max-width: 600px) {
	.banner-text h1 {
		font-size: 3em;
	}
	.social-links i {
		font-size: 3em;
		padding: 4px;
	}
	.banner-text p {
		font-size: 1em;
	}
}
@media (min-width: 300px) and (max-width: 400px) {
	.banner-text h1 {
		font-size: 2em;
	}
	.social-links i {
		font-size: 2em;
		padding: 4px;
	}
	.banner-text p {
		font-size: 1em;
	}
}
@media (min-width: 200px) and (max-width: 300px) {
	.banner-text h1 {
		font-size: 2em;
	}
	.social-links i {
		font-size: 2em;
		padding: 4px;
	}
	.banner-text p {
		font-size: 1em;
	}
}

@media (min-width: 1000px) and (max-width: 1500px) {
	.contact-list i {
		font-size: 30px;
		padding-right: 1rem;
	}
}
@media (min-width: 900px) and (max-width: 1000px) {
	.contact-list i {
		font-size: 20px;
		padding-right: 1rem;
	}
}
@media (min-width: 800px) and (max-width: 900px) {
	.contact-list i {
		font-size: 20px;
		padding-right: 1rem;
	}
}
@media (min-width: 700px) and (max-width: 800px) {
	.contact-list i {
		font-size: 20px;
		padding-right: 1rem;
	}
}
@media (min-width: 600px) and (max-width: 700px) {
	.contact-list i {
		font-size: 20px;
		padding-right: 1rem;
	}
}
@media (min-width: 500px) and (max-width: 600px) {
	.contact-list i {
		font-size: 17px;
		padding-right: 1rem;
	}
}
@media (min-width: 400px) and (max-width: 500px) {
	.contact-list i {
		font-size: 17px;
		padding-right: 1rem;
	}
	.contact-grid {
		width: auto;
	}
}
@media (min-width: 300px) and (max-width: 400px) {
	.contact-list i {
		font-size: 20px;
		padding-right: 0.5rem;
	}
	.contact-grid {
		width: auto;
	}
	.contact-body p {
		width: auto;
	}
}

